import { createApp } from 'vue';
import App from './App.vue';
import router from './router/router';
import FontAwesomeIcon from './plugins/fontawsome.js';

import('bootstrap-vue-next').then(BootstrapVueNext => {
    import('bootstrap/dist/css/bootstrap.css').then(() => {
      import('bootstrap-vue-next/dist/bootstrap-vue-next.css').then(() => {
        app.use(BootstrapVueNext.default);
      });
    });
  });
  

import './styles.css';

const app = createApp(App);

app.use(router);
app.component('font-awesome-icon', FontAwesomeIcon);
app.mount('#app');
